  outline-offset: 2px;
}

/* Better contrast for text */
.text-gray-200 {
  color: #E5E7EB; /* Increased contrast from default */
}

.text-gray-300 {
  color: #D1D5DB; /* Increased contrast from default */
}

.text-gray-400 {
  color: #9CA3AF; /* Increased contrast from default */
}

/* Improved form elements */
input, textarea {
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Better typography */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-space-grotesk), var(--font-inter), sans-serif;
  font-weight: 700;
}

/* Better link styles */
a {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
}

/* Improved animation performance */
* {
  will-change: auto;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #0a0e27;
}

::-webkit-scrollbar-thumb {
  background: #a855f7;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #9333ea;
}

/* Background patterns */
.bg-grid {
  background-image:
    linear-gradient(rgba(255,255,255,0.05) 1px, transparent 1px),
    linear-gradient(to right, rgba(255,255,255,0.05) 1px, transparent 1px);
  background-size: 48px 48px;
}

.bg-dots {
  background-image: radial-gradient(rgba(168,85,247,0.25) 1px, transparent 1px);
